.log {
  background-color: #85a3e0;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(8px + 2vmin);
  color: white;
}

.d0{
  box-sizing: border-box;
  border: 3px solid #E49393;
  padding: 50px;
  background-color: rgb(119, 203, 212);
  border-radius: 10px;
  color: #9d589f;
  
}

.d1{
  margin-bottom: 50px;
  font-weight: bold;
  font-size: 20px;
  
}

.d2{
  background-color: #408E91;
  border-radius: 15px;
  padding: 65px;
  font-size: 15px;
  font-weight: bold;
  text-align: left;
  color: #2D2727;
  margin-bottom: 8px;
  
}

input{
  border: 2px solid black;
  padding: .5rem;
  margin: .2em;
  margin-bottom: 1em;
  
}

button{
  padding: 6px 142px;
  border: 2px solid rgba(255, 60, 60, 0.752);
  border-radius: 8px;
  color: white;
  background-color: rgb(235, 86, 86);
  font-weight: bold;
}
